.payment-modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  backdrop-filter: blur(4px);
  z-index: 1000;
}

.modal-content {
  background: white;
  border-radius: 16px;
  padding: 2rem;
  width: 90%;
  max-width: 400px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
  animation: modalEnter 0.3s ease-out;
}

@keyframes modalEnter {
  from {
    transform: translateY(20px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.5rem;
}

.modal-header h3 {
  margin: 0;
  font-size: 1.25rem;
  color: #2d3748;
}

.close-button {
  background: none;
  border: none;
  font-size: 1.5rem;
  line-height: 1;
  padding: 0 0.5rem;
  cursor: pointer;
  color: #718096;
  transition: color 0.2s;
}

.close-button:hover {
  color: #2d3748;
}

.qrcode-wrapper {
  text-align: center;
  padding: 1rem 0;
  position: relative;
}

.countdown {
  margin-top: 1rem;
  color: #e53e3e;
  font-weight: 500;
  font-size: 0.9rem;
}

.modal-footer {
  margin-top: 1.5rem;
  text-align: center;
}

.modal-footer p {
  color: #718096;
  font-size: 0.9rem;
  margin: 0;
}

/* 二维码样式 */
.react-qr-code {
  border: 1px solid #e2e8f0;
  border-radius: 8px;
  padding: 8px;
  background: white;
}

/* 响应式设计 */
@media (max-width: 480px) {
  .modal-content {
    padding: 1.5rem;
    border-radius: 12px;
  }

  .modal-header h3 {
    font-size: 1.1rem;
  }

  .qrcode-wrapper {
    padding: 0.5rem 0;
  }
}
