.formContainer {
  max-width: 1200px;
  width: 75%;
  margin: 1rem auto;
  padding: 2rem;
  background: rgba(255, 255, 255, 0.95);
  border-radius: 16px;
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.8);
  backdrop-filter: blur(10px);
  position: relative;
}

.formContainer::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: linear-gradient(90deg, #e74c3c, #c0392b);
  border-radius: 16px 16px 0 0;
}

/* 统一表单行样式 */
.formRow {
  margin-bottom: 1.5rem;
  padding: 0.5rem;
}



/* 年月日行样式 */
.formRow1 {
  display: flex;
  justify-content: center;
  gap: 0.8rem;
  margin: 1.5rem 0;
}
.formRow3 {
  display: flex;
  margin-bottom: 2rem;
  padding: 1rem;
  margin-bottom: 0rem;
}
/* 姓名输入样式 */
.inputGroup {
  position: relative;
  width: 90%;
  margin: 0 auto;
}

.underlineInput {
  width: 100%;
  padding: 0.8rem 0;
  border: none;
  border-bottom: 2px solid #d5d5d5;
  font-size: 1.1rem;
  background: transparent;
  transition: all 0.3s ease;
  color: #2c3e50;
  font-family: "Noto Sans SC", sans-serif;
}

.underlineInput:focus {
  outline: none;
  border-color: #c0392b;
  box-shadow: 0 2px 4px rgba(192, 57, 43, 0.1);
}

.underlineInput::placeholder {
  color: #95a5a6;
  font-weight: 300;
}

.optional {
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  color: #95a5a6;
  font-size: 0.9rem;
  font-weight: 300;
  letter-spacing: 1px;
}

/* 其他保持原有样式优化 */
.buttonGroup {
  display: flex;
  gap: 2rem;
  justify-content: center;
  margin: 1rem 0;
}

.choiceButton {
  padding: 0.8rem 1.6rem;
  border: 2px solid #c0392b;
  border-radius: 8px;
  background: white;
  color: #c0392b;
  cursor: pointer;
  transition: all 0.3s ease;
  font-family: "Noto Sans SC", sans-serif;
  position: relative;
  overflow: hidden;
}

.choiceButton::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 0;
  height: 0;
  background: rgba(192, 57, 43, 0.1);
  border-radius: 50%;
  transition: width 0.4s ease, height 0.4s ease;
  transform: translate(-50%, -50%);
}

.choiceButton:hover::after {
  width: 300%;
  height: 300%;
}

.choiceButton.active {
  background: linear-gradient(135deg, #e74c3c 0%, #c0392b 100%);
  color: white;
  border-color: transparent;
  box-shadow: 0 4px 15px rgba(192, 57, 43, 0.2);
}

/* 日期选择容器 */
.dateSelectGroup {
  display: flex;
  gap: 0.5rem;
  width: 100%;
  padding: 0.5rem 0;
  overflow-x: auto; /* 允许横向滚动 */
  scrollbar-width: none; /* 隐藏滚动条 */
  -ms-overflow-style: none;
}

.dateSelectGroup::-webkit-scrollbar {
  display: none;
}

/* 日期选择器基础样式 */
.dateSelect {
  flex: 0 0 calc(33% - 0.5rem);
  min-width: 90px;
  padding: 0.8rem 1rem;
  border: 2px solid #c0392b;
  border-radius: 8px;
  background: white;
  font-size: 0.9rem;
  cursor: pointer;
  white-space: nowrap;
  transition: all 0.3s ease;
  color: #2c3e50;
  font-family: "Noto Sans SC", sans-serif;
  position: relative;
  overflow: hidden;
}

.dateSelect:hover {
  background: rgba(192, 57, 43, 0.05);
  transform: translateY(-1px);
  box-shadow: 0 2px 8px rgba(192, 57, 43, 0.1);
}

/* 时间选择器适配 */
.timeSelectGroup {
  display: flex;
  gap: 0.5rem;
  justify-content: center;
  padding: 0.5rem 0;
}

/* 通用选择器样式优化 */
select {
  appearance: none;
  background-image: url("data:image/svg+xml...");
  background-repeat: no-repeat;
  background-position: right 0.8rem center;
  background-size: 12px;
  font-family: "Noto Sans SC", sans-serif;
}

/* 通用下划线选择样式 */
.underlineSelect {
  width: 200px;
  padding: 0.8rem 0;
  border: none;
  border-bottom: 2px solid #c0392b;
  background: transparent;
  appearance: none;
  font-size: 1rem;
  color: #2c3e50;
  cursor: pointer;
  transition: all 0.3s ease;
  font-family: "Noto Sans SC", sans-serif;
}

.underlineSelect:focus {
  outline: none;
  border-color: #e74c3c;
  box-shadow: 0 2px 4px rgba(192, 57, 43, 0.1);
}

.underlineSelect:hover {
  border-color: #e74c3c;
}




/* 城市选择样式 */
.citySelect {
  width: 80%;
  display: block;
  margin: 0 auto;
  padding: 0.8rem;
  border: 2px solid #c0392b;
  border-radius: 8px;
  background: white;
  color: #2c3e50;
  cursor: pointer;
  transition: all 0.3s ease;
  font-family: "Noto Sans SC", sans-serif;
}

.citySelect:hover {
  border-color: #e74c3c;
  box-shadow: 0 2px 8px rgba(192, 57, 43, 0.1);
  transform: translateY(-1px);
}

.citySelect:focus {
  outline: none;
  border-color: #e74c3c;
  box-shadow: 0 2px 4px rgba(192, 57, 43, 0.1);
}

/* 提交按钮样式 */
.submitButton {
  display: block;
  width: 80%;
  margin: 2rem auto;
  padding: 1.2rem;
  background: linear-gradient(135deg, #e74c3c 0%, #c0392b 100%);
  color: #fff;
  border: none;
  border-radius: 8px;
  font-size: 1.2rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  box-shadow: 0 4px 15px rgba(192, 57, 43, 0.2);
  position: relative;
  overflow: hidden;
  font-family: "Noto Sans SC", sans-serif;
  letter-spacing: 2px;
}

.submitButton::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    90deg,
    transparent 0%,
    rgba(255, 255, 255, 0.2) 50%,
    transparent 100%
  );
  transition: all 0.6s;
}

.submitButton:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(192, 57, 43, 0.3);
  background: linear-gradient(135deg, #c0392b 0%, #e74c3c 100%);
}

.submitButton:hover::before {
  left: 100%;
}

.submitButton:active {
  transform: translateY(1px);
  box-shadow: 0 2px 10px rgba(192, 57, 43, 0.2);
}

/* 响应式调整 */
@media (max-width: 480px) {
  .formRow1 {
    flex-wrap: wrap;
  }
  
  .dateSelectGroup {
    flex-wrap: wrap;
  }
  
  .dateSelect {
    flex: 1 1 30%;
    min-width: auto;
  }
}