.analysis-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
  position: relative;
  font-family: "Noto Sans SC", sans-serif;
}

/* 操作按钮 */
.action-button {
  position: fixed;
  bottom: 1rem;
  padding: 0.8rem 1.5rem;
  background: rgba(255, 255, 255, 0.95);
  border: 2px solid #c0392b;
  border-radius: 12px;
  cursor: pointer;
  color: #c0392b;
  box-shadow: 0 4px 15px rgba(192, 57, 43, 0.1);
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  z-index: 1000;
  font-size: 1rem;
  font-family: "Noto Sans SC", sans-serif;
  backdrop-filter: blur(10px);
}

.action-button.back { left: 1.5rem; }
.action-button.screenshot { right: 1.5rem; }

.action-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(192, 57, 43, 0.15);
  background: rgba(192, 57, 43, 0.05);
}

/* 预览内容区域 */
.preview-section {
  background: rgba(255, 255, 255, 0.95);
  padding: 2.5rem;
  border-radius: 16px;
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.1);
  margin: 2.5rem 0;
  border: 1px solid rgba(255, 255, 255, 0.8);
  backdrop-filter: blur(10px);
  font-family: "Noto Serif SC", serif;
  line-height: 1.8;
  color: #2c3e50;
  position: relative;
}

.preview-section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: linear-gradient(90deg, #e74c3c, #c0392b);
  border-radius: 16px 16px 0 0;
}

/* 分析模块布局 */
.analysis-modules {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 20px;
  margin: 2.5rem 0;
}

/* 单个分析模块 */
.analysis-module {
  border-radius: 16px;
  padding: 1.5rem;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  color: white;
  position: relative;
  overflow: hidden;
  min-height: 140px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  border: 1px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 8px 25px rgba(192, 57, 43, 0.15);
}

/* 模块交互状态 */
.analysis-module:hover {
  transform: translateY(-3px);
  box-shadow: 0 12px 30px rgba(192, 57, 43, 0.2);
}

.analysis-module.active {
  transform: scale(1.03);
  box-shadow: 0 15px 35px rgba(192, 57, 43, 0.25);
  z-index: 1;
}

/* 模块解锁状态指示 */
.analysis-module::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, rgba(0,0,0,0.5) 0%, rgba(0,0,0,0.3) 100%);
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
}

.analysis-module.unlocked::after {
  opacity: 0;
  backdrop-filter: blur(0);
}

/* 模块内部元素 */
.module-header {
  display: flex;
  align-items: center;
  gap: 12px;
  margin-bottom: 15px;
  position: relative;
  z-index: 1;
}

.module-header .icon {
  font-size: 2em;
  filter: drop-shadow(0 2px 4px rgba(0,0,0,0.2));
  transition: transform 0.3s ease;
}

.analysis-module:hover .module-header .icon {
  transform: scale(1.1) rotate(-5deg);
}

.module-header h3 {
  margin: 0;
  font-size: 1.2em;
  font-family: "Noto Sans SC", sans-serif;
  text-shadow: 0 2px 4px rgba(0,0,0,0.2);
  font-weight: 500;
  letter-spacing: 1px;
}

/* 模块状态 */
.module-status {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  font-size: 0.9em;
  opacity: 0.9;
  text-align: right;
}

/* 分享相关 */
.share-section {
  text-align: center;
  margin: 2rem 0;
}

.share-container {
  position: relative;
  display: inline-block;
  margin: -20px 0;
}

.tooltip {
  visibility: hidden;
  width: 200px;
  background: rgba(44, 62, 80, 0.95);
  color: #fff;
  text-align: center;
  border-radius: 12px;
  padding: 12px 16px;
  position: absolute;
  z-index: 1;
  bottom: 125%;
  left: 50%;
  transform: translateX(-50%) translateY(10px);
  opacity: 0;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  font-family: "Noto Sans SC", sans-serif;
  font-size: 0.9rem;
  letter-spacing: 1px;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.15);
}

.share-container:hover .tooltip {
  visibility: visible;
  opacity: 1;
  transform: translateX(-50%) translateY(0);
}

.copied-notice {
  position: fixed;
  top: 20px;
  right: 20px;
  background: linear-gradient(135deg, #e74c3c, #c0392b);
  color: white;
  padding: 15px 25px;
  border-radius: 12px;
  box-shadow: 0 8px 25px rgba(192, 57, 43, 0.2);
  animation: slideIn 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  font-family: "Noto Sans SC", sans-serif;
  font-size: 0.95rem;
  letter-spacing: 1px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  z-index: 1001;
}

@keyframes slideIn {
  from { transform: translateX(100%); }
  to { transform: translateX(0); }
}

.share-trigger {
  background: linear-gradient(135deg, #e74c3c, #c0392b);
  color: white;
  padding: 1rem 2rem;
  border-radius: 12px;
  border: none;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  box-shadow: 0 4px 15px rgba(192, 57, 43, 0.2);
  font-family: "Noto Sans SC", sans-serif;
  font-size: 1rem;
  letter-spacing: 1px;
  position: relative;
  overflow: hidden;
  display: block;
  margin: 1.5rem auto;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.share-trigger::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    90deg,
    transparent 0%,
    rgba(255, 255, 255, 0.2) 50%,
    transparent 100%
  );
  transition: all 0.6s;
}

.share-trigger:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(192, 57, 43, 0.3);
  background: linear-gradient(135deg, #c0392b, #e74c3c);
}

.share-trigger:hover::before {
  left: 100%;
}

.share-trigger:active {
  transform: translateY(1px);
}

/* 加载动画 */
.loader {
  display: inline-block;
  width: 20px;
  height: 20px;
  border: 2px solid transparent;
  border-top: 2px solid #fff;
  border-radius: 50%;
  vertical-align: middle;
  animation: spin 0.8s linear infinite;
}

@keyframes spin {
  to { transform: rotate(360deg); }
}

/* 详细内容区域 */
.detail-content {
  margin-top: 30px;
  padding: 25px;
  background: #fff;
  border-radius: 10px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  animation: fadeIn 0.3s ease;
}

.detail-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  padding-bottom: 15px;
  border-bottom: 1px solid #eee;
}

.detail-header h3 {
  margin: 0;
  color: #2c3e50;
  font-size: 1.4rem;
  font-weight: 500;
}

.refresh-button {
  background: #fff;
  border: 1px solid transparent;
  color: #c0392b;
  padding: 8px 16px;
  border-radius: 8px;
  cursor: pointer;
  font-size: 0.9rem;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: 6px;
}

.refresh-button:hover {
  background: #c0392b;
  color: #fff;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(192, 57, 43, 0.15);
}

.refresh-button:active {
  transform: translateY(0);
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(10px); }
  to { opacity: 1; transform: translateY(0); }
}

/* 模糊效果 */
.blur-section {
  position: relative;
  transition: filter 0.3s ease;
}

.blur-section.locked {
  filter: blur(8px);
  pointer-events: none;
}

.blur-section.locked::after {
  content: "解锁任意领域查看完整分析";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #2c3e50;
  font-weight: bold;
  z-index: 1;
  background: rgba(255,255,255,0.9);
  padding: 10px 20px;
  border-radius: 30px;
  box-shadow: 0 2px 6px rgba(0,0,0,0.1);
}

/* 免责声明 */
.disclaimer {
  margin-top: 40px;
  padding: 20px;
  background: #fff;
  border-radius: 10px;
  font-size: 0.7em;
  color: #af3333;
  text-align: center;
}

.disclaimer p {
  margin: 5px 0;
}

/* 响应式设计 */
@media (max-width: 768px) {
  .analysis-modules {
    grid-template-columns: repeat(2, 1fr);
    gap: 15px;
  }
  
  .preview-section {
    padding: 1.5rem;
    margin: 1.5rem 0;
  }
  
  .copied-notice {
    top: 10px;
    right: 10px;
    font-size: 14px;
  }
}

@media (max-width: 480px) {
  .analysis-module {
    padding: 15px;
    min-height: 100px;
  }
  
  .module-header h3 {
    font-size: 1em;
  }
  
  .module-status {
    font-size: 0.8em;
  }
  
  .action-button {
    padding: 0.5rem 0.4rem;
    font-size: 0.85rem;
  }
}

/* 在文件末尾添加 */
.analysis-progress-container {
  width: 100%;
  height: 8px;
  background-color: rgba(255, 255, 255, 0.3);
  border-radius: 4px;
  margin: 5px 0;
  overflow: hidden;
}

.analysis-progress-bar {
  height: 100%;
  background: linear-gradient(90deg, rgba(255, 255, 255, 0.9) 0%, rgba(255, 255, 255, 0.7) 100%);
  transition: width 0.3s ease;
  border-radius: 4px;
}

.analysis-progress-percentage {
  font-size: 12px;
  color: rgba(255, 255, 255, 0.9);
  margin-top: 2px;
  font-weight: bold;
}

/* 添加进度文本的样式 */
.progress-text {
  font-size: 12px;
  color: rgba(255, 255, 255, 0.9);
  margin-top: 2px;
  font-weight: bold;
  animation: pulse 1.5s infinite;
}

@keyframes pulse {
  0% { opacity: 0.6; }
  50% { opacity: 1; }
  100% { opacity: 0.6; }
}
